import * as React from "react";
import IconButton from "@mui/material/IconButton";
import Box from "@mui/material/Box";
import { useTheme, ThemeProvider, createTheme } from "@mui/material/styles";
import Brightness4Icon from "@mui/icons-material/Brightness4";
import Brightness7Icon from "@mui/icons-material/Brightness7";
import { useState } from "react";
import WbSunnyIcon from "@mui/icons-material/WbSunny";
import NightsStayIcon from "@mui/icons-material/NightsStay";
const ColorModeContext = React.createContext({ toggleColorMode: () => {} });

export default function DarkMode() {
  const [modeName, setModeName] = useState("light");

  const changeMode = () => {
    if (modeName == "dark") {
      document.documentElement.style.setProperty("--body-bg", "white");
      document.documentElement.style.setProperty("--body-color", "black");
      document.documentElement.style.setProperty("--left-bg", "#253445");
      document.documentElement.style.setProperty("--left-color", "white");

      setModeName("light");
    } else {
      document.documentElement.style.setProperty("--body-bg", "rgb(17 17 17)");
      document.documentElement.style.setProperty("--body-color", "white");
      document.documentElement.style.setProperty("--left-bg", "rgb(152 0 0)");
      document.documentElement.style.setProperty("--left-color", "white");

      setModeName("dark");
    }
  };

  return (
    <Box
      position={"fixed"}
      right={"10px"}
      bottom="10px"
      width="fit-content"
      height="fit-content"
    >
      <Box
        sx={{
          display: "flex",
          width: "fit-content",
          height: "fit-content",
          alignItems: "center",
          justifyContent: "center",
          bgcolor: "background.default",
          color: "text.primary",
          borderRadius: 1,

          p: 3,
        }}
      >
        <IconButton onClick={changeMode} color="inherit">
          {modeName === "dark" ? (
            <WbSunnyIcon
              sx={{
                width: "3rem",
                height: "3rem",
              }}
            />
          ) : (
            <NightsStayIcon
              sx={{
                width: "3rem",
                height: "3rem",
              }}
            />
          )}
        </IconButton>
      </Box>
    </Box>
  );
}
