function Portfolio({ data }) {
  return (
    <div>
      <h1>Projects</h1>
      {data.map(
        (
          {
            title,
            tech,
            type,
            links,
            site_purpose,
            functionalities,
            technologies,
          },
          index
        ) => {
          return (
            <div>
              <h2>
                {index + 1}. {title}
              </h2>
              <h4>
                {tech && (
                  <>
                    <span>{tech}</span>-
                  </>
                )}

                {type && (
                  <>
                    <span>{type}</span>
                  </>
                )}
              </h4>
              {links.map(({ name, link }) => (
                <p>
                  {name}: <a href={link}>{link}</a>
                </p>
              ))}
              {site_purpose && (
                <>
                  <h4>Site purpose</h4>
                  <p>{site_purpose}</p>
                </>
              )}
              {functionalities.length > 0 && (
                <>
                  <h4>Functionalities</h4>
                  <ul>
                    {functionalities.map((functionality) => (
                      <li>{functionality}</li>
                    ))}
                  </ul>
                </>
              )}
              {technologies && (
                <>
                  <h4>Technologies</h4>
                  <p>{technologies}</p>
                </>
              )}
            </div>
          );
        }
      )}
    </div>
  );
}

export default Portfolio;
