function AboutMe({
  data: {
    full_name,
    birth_location,
    nationality,
    family_situation,
    phone_and_whatsapp,
    email,
    linkedin,
  },
}) {
  return (
    <div>
      <h1>{full_name}</h1>
      <h2>About Me</h2>
      <p>Birth Location: {birth_location}</p>
      <p>Nationality: {nationality}</p>
      <p>Family Situation: {family_situation}</p>

      <h2>Contact</h2>
      <p>Phone &amp; Whatsapp: {phone_and_whatsapp}</p>
      <p>Email: {email}</p>
      <p>
        Linkedin: <a href={linkedin}>{linkedin}</a>
      </p>
    </div>
  );
}

export default AboutMe;
